$(function(){

var content_pos = $('#content').position().top;
var subHeader = $('#subHeader');
var pagetop = $('.pagetop');
var scrollFlag = false;
$(window).on('scroll', function(){
    var pos = $(this).scrollTop();
    content_pos = $('#content').position().top;
    if(pos >= content_pos){
        if(!scrollFlag) {
            scrollFlag = true;
            subHeader.addClass('show');
            pagetop.velocity("fadeIn", { duration: 500 });
        }
    } else {
        if(scrollFlag) {
            subHeader.removeClass('show');
            pagetop.velocity("fadeOut", { duration: 500 });
            scrollFlag = false;
        }
    }
});
$(window).on('resize', function(){
    content_pos = $('#content').position().top;
});

var ua = window.navigator.userAgent.toLowerCase();
if(ua.match(/undotsushin/)) {
    $('body').addClass('mode-app');
}

$(document).on('touchend', '#anchor__contens a', function(){
    var hash = $(this).attr('href');
    $(hash).velocity("scroll", { duration: 500, offset: -60, easing: "ease" });
    return false;
});
$(document).on('click', '#anchor__contens a', function(){
    var hash = $(this).attr('href');
    $(hash).velocity("scroll", { duration: 500, offset: -60, easing: "ease" });
    return false;
});

pagetop.find('a').on('click', function(){
    $('#header').velocity("scroll",
        { duration: 500, easing: "ease" }
    );
    return false;
});

});
